.header {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: 0.5em;
	justify-content: space-between;

	padding: 0 .5em;

	h1 {
		margin-right: 1rem;
	}

	.actions {
		display: flex;
		align-items: center;

		.button {
			&:not(:last-child) {
				margin-right: .5em;
			}
		}

		.action {
			cursor: pointer;
			border: 1px solid;
			height: 2em;
			width: 2em;
			display: flex;
			align-items: center;
			justify-content: center;

			&:not(:last-child) {
				margin-right: .5em;
			}

			svg {
				display: block;
				width: 1.125em;
			}
		}
	}
}