.bar-chart {
  width: 100%;
  margin-bottom: 1em;

  .chart-header {
    font-family: $quicksand;
    font-size: 0.8em;
    font-weight: 700;
    line-height: 1em;

    padding: 0.375em 0.5em;
  }

  .chart-body {
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 0.5em 0.25em 0;
    height: 13.125em;
    border: 1px solid;

    display: flex;
    align-items: flex-end;

    .bar {
      position: relative;
      min-width: 1.75em;
      margin: 0 0.25em;
      border: 1px solid;
      border-bottom: none;
      text-align: center;

      &:last-child:after {
        content: "";
        display: block;
        right: -0.5em;
        width: 0.5em;
        height: 1px;
        position: absolute;
      }

      .bar-value {
        font-size: 0.9em;
        min-height: 1.25em;
        padding: 0.25em 0;
      }

      .bar-element {
        $i: 0;

        @while $i <= 100 {
          &.height-#{$i} {
            height: $i * 0.1em;
          }
          $i: $i + 1;
        }
      }
    }
  }
}
